/* Standard Font */

  @font-face {
      font-family: Liberation Mono;
      src: url('../public/Liberation.ttf');
  }

/* Syntax Highlighting */

  .MeteorToys_string, .Mongol_string {
    color: #A6E22D; /* !important; /* green */
  }
  
  .MeteorToys_number, .Mongol_number {
    color: #E6DB74; /* !important; /* green-yellow */
  } 
  
  .MeteorToys_boolean, .Mongol_boolean {
    color: #66D9EF; /* !important; /* dark orange */
  } 
  
  .MeteorToys_null, .Mongol_null {
    color: #AE81FF; /* !important; /* blue */
  } 
  
  .MeteorToys_key, .Mongol_key {
    color: #F92772; /* !important; /* magenta */
  } 

/* Err */

.MeteorToys {
  background-color: rgba(63,63,63,.92);
  -webkit-font-smoothing: antialiased;
  font-family: Liberation Mono, "Menlo", Courier, Lucidatypewriter, Fixed, monospace;
  position: fixed;
  transition: 0.3s;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  color: #fff;
  font-size: 14px;
  line-height: 28px;
  padding: 0px;
  margin: 0px;
  z-index: 2147483646;
  color: rgba(255, 255, 255, 0.6);
  cursor: default;
}

.MeteorToys * { 
  outline: none;
}

.MeteorToys strong {
  color: rgba(255,255,255,.8);
  font-family: Liberation Mono, Courier, Lucidatypewriter, Fixed, monospace !important; 
  font-weight: bold;
}

#MeteorToys_ToyKit { 
  width: 100%;
  box-shadow: inset 0 0 0 30px rgba(0,0,0,.3);
}
#MeteorToys_ToyKit strong {
  padding-left: 8px;
} 
#MeteorToys_ToyKit div {
  float: right;
  padding: 0 8px;
  cursor: pointer;
  border-left: 1px solid rgba(255,255,255,.2);
}
/*#MeteorToys_ToyKit div:hover;*/

  .MeteorToys_orbs {
    height: 100px;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 2147483647;
    line-height: 20px;
    pointer-events: none;
    color: rgba(255, 255, 255, 0.6);
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    font-family: 'Liberation Mono', Courier, Lucidatypewriter, Fixed, monospace;
    font-size: 14px;
  }

/* ORB UI */

  .MeteorToys_orb {
    height: 46px;
    width: 46px;
    background: rgba(63,63,63,.92);
    float: left;
    margin-left: 8px;
    

    margin-top: 0px;
    
    pointer-events:all;
    transition: .25s;
    overflow: hidden;
    
    position: relative;
    cursor: default;
    max-width: 600px;
    max-height: 600px;
    border-radius: 0 0 12px 12px;
  }

  .MeteorToys_orb_condensed {
    height: 46px !important;
    width: 46px !important;
    border-radius: 23px;
    cursor: pointer;
    margin-top: 8px;
  }

  .MeteorToys_orb_wrapper {
    margin: 8px;
    box-shadow: 0 0 0 75px rgba(0,0,0,.36);
    border-radius: 5px;
    height: calc(100% - 16px);
    overflow: hidden;
    margin-top: 74px;
    transition: .25s;
  }

  .MeteorToys_orb:hover .MeteorToys_orb_wrapper {
    box-shadow: 0 0 0 75px rgba(0,0,0,.86);
  }

  .MeteorToys_orb:hover .MeteorToys_icon {
    opacity: .8 !important;
  }

  .MeteorToys_orb_active .MeteorToys_orb_wrapper {
    box-shadow: 0 0 0 75px rgba(0,0,0,.86);
    margin-top: 8px;

  }

  .MeteorToys_orb_active {
    z-index: 2147483647 !important;
  }

  .MeteorToys_orb .MeteorToys_icon { 
    transition: .25s;
    position: absolute;
    top: 0;
    left: 0;
    opacity: .6;
    height: 46px;
    width: 46px;
    background-size: cover;
  }
  .MeteorToys_orb_active .MeteorToys_icon { 
    top: -46px;
  }

/* Button UI */

  .MeteorToys_button {
    margin-top: 8px !important;
    border-radius: 23px !important;
    cursor: pointer;
  }

  .MeteorToys_button .MeteorToys_icon {
    top: 0px !important;
  }

  .MeteorToys_button .MeteorToys_orb_wrapper {
    height: 0px;
    width: 0px;
  }
  .MeteorToys_button:active .MeteorToys_orb_wrapper {
    box-shadow: 0 0 0 75px rgba(0,0,0,.9);
    box-shadow: 0 0 0 75px rgba(0,0,0,0) !important; 
  }

.MeteorToys_row { 
  border-bottom: 1px solid rgba(0,0,0,.36);
  padding: 4px 0px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.MeteorToys_row:last-child {
  border-bottom: 0px solid transparent;
}
.MeteorToys_row:nth-child(-n+9) {
  border-bottom: 1px solid rgba(0,0,0,.36);
}
.MeteorToys_row_hoverable {

}
.MeteorToys_row_hoverable:hover {
  /*color: rgba(131, 202, 237, 1);*/
  
  background: rgba(255,255,255,.1);
  border-bottom: 1px solid rgba(0,0,0,.44) !important;
  cursor: pointer;

  /**/

}

.MeteorToys_row_expanded {
  border-bottom: 1px solid rgba(0,0,0,.36);
  padding: 4px 0;
}
.MeteorToys_row_expanded:last-child {
  border-bottom: 0px solid transparent;
}
.MeteorToys_row_expanded:nth-child(-n+5) {
  border-bottom: 1px solid rgba(0,0,0,.36);
}

/* Form Resets */
  /*, .MeteorToys {}*/

  .MeteorToys_orbs input,
  .MeteorToys_orbs label,
  .MeteorToys_orbs select,
  .MeteorToys_orbs button,
  .MeteorToys_orbs textarea {
    margin:0;
    border:0;
    padding:0;
    display:inline-block;
    vertical-align: top;
    white-space:normal;
    background: transparent;
    line-height: inherit;
    font-size: inherit;
    font-family: inherit;
    outline:0;
    /*color: rgba(255,255,255,.6);*/
    color: inherit;
    resize: none;
  }
  .MeteorToys_orbs textarea {
    width: 100%;
  }
  .MeteorToys_orbs input[type="submit"] {
    color: #A6E22D;
    float: right;
    padding: 8px 0;
  }

  .MeteorToys_row input {
    width: 62%;
    float: right;
    margin: -4px 0;
    padding: 4px 0;
    background: rgba(255,255,255,.1) !important;
    text-indent: 5px;
    margin-left: 8px;
  }

  .MeteorToys_row_name { 
    width: calc(38% - 8px); 
      white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  }

  .MeteorToys_orbs ::-webkit-input-placeholder {
     color: rgba(255,255,255,.4) !important;
  }

  .MeteorToys_orbs :-moz-placeholder { 
     color: rgba(255,255,255,.4) !important;  
  }

  .MeteorToys_orbs ::-moz-placeholder { 
     color: rgba(255,255,255,.4) !important;  
  }

  .MeteorToys_orbs :-ms-input-placeholder {  
     color: rgba(255,255,255,.4) !important;  
  }

  .MeteorToys form, .MeteorToys_orbs form {
    line-height: inherit;
    padding: auto;
    margin: auto;
  }